<template>
    <Head head-key="title" title="Alt Kategoriler" />

    <div id="kt_app_toolbar" class="app-toolbar py-3 py-lg-6">
        <div id="kt_app_toolbar_container" class="app-container container-fluid d-flex flex-stack">
            
            <PageTitleWithLanguage :routeName="'photo-sub-categories.index'" :title="'Alt Kategori'"/>

        </div>
    </div>
	
    <div id="kt_app_content" class="app-content  flex-column-fluid">
        <div id="kt_app_content_container" class="app-container container-fluid">
            
            <div class="card">

                <div class="card-header px-4">
                    <div class="card-toolbar w-100 justify-content-between gap-2">
                        <Create :language="$page.props.current_language" :photo-categories="props.photo_categories" :known-names="props.known_names"/>
                        <div class="d-flex flex-grow-1 px-3">
                            <Filter :filters="props.filters" :uri="route('photo-sub-categories.index')"/>
                        </div>
                        <Link :href="route('photo-sub-categories.trash',{language:$page.props.current_language})" class="btn fw-bold btn-sm btn-light-danger btn-icon ms-auto" v-tooltip.top="'Çöp Kutusu'">
                            <i class="bi bi-trash fs-4"></i>
                        </Link>
                        <Link :href="route('photo-sub-categories.reorder',{language:$page.props.current_language})" class="btn fw-bold btn-sm btn-light d-flex align-items-center">
                            <i class="bi bi-sort-down fs-4"></i>
                            <span class="ms-1 lh-1">SIRALA</span>
                        </Link>
                    </div>
                </div>

                <div class="card-header px-4" v-if="selectedItems.length > 0">
                    <h5 class="card-title fs-6"><i class="bi bi-check2-circle me-2 fs-2"></i>{{ selectedItems.length }}</h5>
                    <div class="card-toolbar">
                        <DeleteBulkPrompt :uri="route('photo-sub-categories.bulk.delete',{language:$page.props.current_language})" :items="selectedItems" :delete-done="resetSelectedItems"/>
                    </div>
                </div>

                <div class="card-body p-0">

                    <DataTable v-model:selection="selectedItems" scrollable :value="props.photo_sub_categories" stripedRows dataKey="id" tableStyle="min-width: 50rem">
                        <template #header>
                            <div>
                                <MultiSelect :modelValue="selectedColumns" :options="columns" optionLabel="header" @update:modelValue="onToggle" display="chip" placeholder="Select Columns" class="w-100"/>
                            </div>
                        </template>
                        <template #empty> Seçilen kriterlere uygun kayıt bulunamadı. </template>
                        <Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
                        <Column headerStyle="width: 3rem">
                            <template #body="{ data }">
                                <div v-if="!data.is_published" v-tooltip.top="'Yayıla'">
                                    <PublishPrompt :uri="route('photo-sub-categories.publish',[data.id,{language:$page.props.current_language}])" :model="data"/>
                                </div>
                                <div v-if="data.is_published" v-tooltip.top="'Yayından Kaldır'">
                                    <UnPublishPrompt :uri="route('photo-sub-categories.unpublish',[data.id,{language:$page.props.current_language}])" :model="data"/>
                                </div>
                            </template>
                        </Column>
                        <Column field="id" header="ID" headerStyle="width: 4rem" sortable></Column>
                        <Column header="Bağlı Diller">
                            <template #body="{ data }">
                                <div class="hstack gap-2">
                                    <div v-for="(lang,key) in $page.props.languages.active.filter((l) => l != data.language)" :key="key">
                                        <Create :button-class="'btn fw-bold btn-sm btn-icon btn-light-danger text-uppercase'" :button-text="lang" :language="lang" :from-attribute="data" :photo-categories="props.photo_categories" v-if="!hasConnectedLanguage(data,lang)" v-tooltip.top="'Oluştur'"/>
                                        <button type="button" v-if="hasConnectedLanguage(data,lang)" @click="selectedAttribute = hasConnectedLanguage(data,lang)" class="btn fw-bold btn-sm btn-icon btn-light-info text-uppercase" v-tooltip.top="'Güncelle'">{{ lang }}</button>
                                    </div>
                                </div>
                            </template>
                        </Column>

                        <Column v-for="(col, index) of selectedColumns" :field="getField(col)" :header="col.header" :key="col.field + '_' + index" class="text-nowrap" sortable>
                            <template #body="{ data }">
                                <span v-if="col.field == 'created_at' || col.field == 'published_at'">{{ formatDateToMask(data[col.field], true) }}</span>
                                <span v-if="col.field == 'user_id'">{{ data.user?.name }}</span>
                                <span v-if="col.field == 'published_by'">{{ data.publisher?.name }}</span>
                                <span v-if="col.field == 'photo_category'">{{ data.photo_category?.name }}</span>
                                <span v-if="col.useField">{{ data[col.field] }}</span>
                            </template>
                        </Column>

                        <Column class="text-end border-start" frozen alignFrozen="right" header="İşlemler">
                            <template #body="{ data }">
                                <div class="d-flex flex-nowrap justify-content-end">
                                    <a :href="`${data.uri.final_uri}`" target="_blank" class="ms-2 btn btn-sm text-success btn-link">
                                        <i class="bi bi-eye"></i>
                                    </a>
                                    <Button icon="bi bi-pen" text rounded severity="info" @click="selectedAttribute = data" v-tooltip.top="'Güncelle'"/>
                                    <DeletePrompt :uri="route('photo-sub-categories.delete',[data.id,{language:$page.props.current_language}])" :model="data"/>
                                </div>
                            </template>
                        </Column>
                    </DataTable>

                </div>
            </div>

        </div>

    </div>

    <Edit :item="selectedAttribute" :on-hide="resetSelectedAttribute" :photo-categories="props.photo_categories" :known-names="props.known_names" v-if="selectedAttribute"/>

</template>

<script setup>

import {ref} from "vue";

import PageTitleWithLanguage from "../../Components/PageTitleWithLanguage";

import Create from "./Create";
import Edit from "./Edit";
import DeletePrompt from "../../Components/DeletePrompt";
import DeleteBulkPrompt from "../../Components/DeleteBulkPrompt";
import PublishPrompt from "../../Components/PublishPrompt";
import UnPublishPrompt from "../../Components/UnPublishPrompt";
import DataTable from "primevue/datatable";
import Column from 'primevue/column';
import Button from "primevue/button";
import Filter from "../../Components/Filter";
import MultiSelect from "primevue/multiselect";

const props = defineProps({
    photo_categories:Object,
    photo_sub_categories:Object,
    known_names:{
        type:Array,
        default:[]
    },
    filters:Object
});

const selectedItems = ref([]);
const metaKey = ref(true);

const resetSelectedItems = () => {
    selectedItems.value = [];
}

const selectedAttribute = ref(null);

const resetSelectedAttribute = () => {
    selectedAttribute.value = null;
}

const columns = ref([
    {field: 'name', header: 'İsim',useField:true},
    {field: 'name_known', header: 'Bilinen İsim',useField:true},
    {field: 'photo_category', header: 'Kategori'},
    {field: 'photo_families_count', header: 'Aile Adeti',useField:true},
    {field: 'photo_types_count', header: 'Tür Adeti',useField:true},
    {field: 'photos_count', header: 'Fotoğraf Adeti',useField:true},
    {field: 'created_at', header: 'Oluşturulma Tarihi'},
    {field: 'user_id', header: 'Oluşturan'},
    {field: 'published_at', header: 'Yayın Tarihi'},
    {field: 'published_by', header: 'Yayınlayan'},
]);
const selectedColumns = ref(columns.value);

const onToggle = (val) => {
    selectedColumns.value = columns.value.filter(col => val.includes(col));
};

const getField = (column) => {

    if( column.field == 'photo_category' ){
        return 'photo_category.name';
    }

    return column.field;

}

const hasConnectedLanguage = (content,lang) => {

    let data = null;
    const dfilter = content.connected_photo_sub_categories.filter((x) => x.language == lang);

    if( dfilter.length ){
        data = dfilter[0];
    }
    return data;

}

const formatDateToMask = (date, withHour = false) => {

    if(!date) return '';
    
    var originalDate = new Date(date);
    var day = originalDate.getDate().toLocaleString().padStart(2, '0');
    var month = (originalDate.getMonth() + 1).toString().padStart(2, '0'); // Ay 0-11 arasında olduğu için +1 ekliyoruz.
    var year = originalDate.getFullYear();
    var hour = String(originalDate.getHours()).padStart(2, '0');
    var min = String(originalDate.getMinutes()).padStart(2, '0');
    var sec = String(originalDate.getSeconds()).padStart(2, '0');

    if( withHour ){
        return day + '.' + month + '.' + year + ' ' + hour + ':' + min + ':' + sec;
    }
    return day + '.' + month + '.' + year;
}

</script>